<template>
  <div id="app">
    <!-- 抽屉式导航 -->
    <app-sidebar
      :sidebar-width="sidebarWidth"
      :sidebar-toggle-width="sidebarToggleWidth"
      :is-drawer-open="isDrawerOpen"
      @toggle-sidebar="toggleSidebar"
      @close-sidebar="closeSidebar"
    ></app-sidebar>
    <!-- 应用主体 -->
    <app-main
      :sidebar-width="sidebarWidth"
      :sidebar-toggle-width="sidebarToggleWidth"
      :is-drawer-open="isDrawerOpen"
      @toggle-sidebar="toggleSidebar"
    ></app-main>
  </div>
</template>

<script>
import AppSidebar from '@/components/AppSidebar.vue'
import AppMain from '@/components/AppMain.vue'

export default {
  name: 'app',

  components: {
    'app-main': AppMain,
    'app-sidebar': AppSidebar
  },

  data () {
    return {
      sidebarWidth: 256,
      sidebarToggleWidth: 768,
      isDrawerOpen: false
    }
  },

  created () {
    if (window.innerWidth >= this.sidebarToggleWidth) {
      this.isDrawerOpen = true
    }
  },

  methods: {
    toggleSidebar () {
      this.isDrawerOpen = !this.isDrawerOpen
    },

    closeSidebar () {
      if (window.innerWidth <= this.sidebarToggleWidth) {
        this.isDrawerOpen = false
      }
    }
  }
}
</script>
