<template>
  <mu-list
    :value="selectedValue"
    toggle-nested
    @change="itemSelected"
  >
    <!-- list-item -->
    <mu-list-item
      v-for="item of items"
      :key="item.value"
      :value="item.children ? '' : item.value"
      :to="item.children ? '' : item.path"
      :open="openedValue === item.value"
      button
      nested
      @toggle-nested="openedValue = arguments[0] ? item.value : '2'"
    >
      <mu-list-item-action>
        <mu-icon
          :value="item.icon"
          :color="selectedValue === item.value ? 'primary' : ''"
        ></mu-icon>
      </mu-list-item-action>
      <mu-list-item-title>
        {{item.title}}
      </mu-list-item-title>
      <mu-list-item-action
        v-if="item.children"
      >
        <mu-icon
          :class="['toggle-icon', {'toggle-icon-rotate': openedValue === item.value}]"
          value="keyboard_arrow_down"
        ></mu-icon>
      </mu-list-item-action>
      <!-- nested-list -->
      <mu-list-item
        v-for="subItem in item.children"
        :key="subItem.value"
        slot="nested"
        :value="subItem.value"
        :to="item.path + subItem.path"
        button
      >
        <mu-list-item-title>
          {{subItem.title}}
        </mu-list-item-title>
      </mu-list-item>
    </mu-list-item>
  </mu-list>
</template>

<script>
export default {
  name: 'app-sidebar-list',

  data () {
    return {
      openedValue: 'home',
      selectedValue: 'home',
      items: [
        {
          path: '/home',
          icon: 'home',
          value: 'home',
          title: '首页'
        },
        {
          path: '/music',
          icon: 'library_music',
          value: 'music',
          title: '音乐'
        },
        {
          path: '/video',
          icon: 'video_library',
          value: 'video',
          title: '视频'
        },
        {
          path: '/article',
          icon: 'library_books',
          value: 'article',
          title: '文章',
          children: [
            {
              path: '/red',
              value: 'red',
              title: '红色'
            },
            {
              path: '/blue',
              value: 'blue',
              title: '蓝色'
            }
          ]
        }
      ]
    }
  },

  created () {
    for (const item of this.items) {
      if (this.$route.path.includes(item.path)) {
        this.openedValue = item.value
      }
    }
    this.selectedValue = this.$route.path.split('/').pop()
    this.$router.afterEach((to, from) => {
      this.selectedValue = to.path.split('/').pop()
      this.items.forEach(item => {
        if (to.path.includes(item.path)) {
          this.openedValue = item.value
        }
      })
    })
  },

  methods: {
    itemSelected (value) {
      if (value) {
        this.selectedValue = value
        this.$emit('toggle-sidebar')
      }
    }
  }
}
</script>

<style scoped>
.toggle-icon {
  transition: transform .3s cubic-bezier(.23,1,.32,1);
}

.toggle-icon-rotate {
  transform: rotateZ(180deg);
}
</style>
