<template>
  <mu-appbar
    id="app-main-header"
    color="primary"
    :style="styleAppMainHeader"
    :title="title"
  >
    <mu-button
      slot="left"
      icon
      @click="toggleSidebar"
    >
      <mu-icon value="menu"></mu-icon>
    </mu-button>
    <mu-button
      slot="right"
      icon
      @click="openOldPage"
    >
      <mu-icon value="compare"></mu-icon>
    </mu-button>
  </mu-appbar>
</template>

<script>
export default {
  name: 'app-main-header',

  props: {
    sidebarWidth: Number,
    isDrawerOpen: Boolean,
    isOffsetLeft: Boolean
  },

  computed: {
    title () {
      let tempTitle = '404';
      [
        {
          path: '/home',
          title: '首页'
        },
        {
          path: '/music',
          title: '音乐'
        },
        {
          path: '/video',
          title: '视频'
        },
        {
          path: '/article',
          title: '文章'
        }
      ].forEach(item => {
        if (this.$route.path.includes(item.path)) {
          tempTitle = item.title
        }
      })
      return tempTitle
    },
    styleAppMainHeader () {
      return {
        left: `${this.isDrawerOpen && this.isOffsetLeft ? this.sidebarWidth : 0}px`
      }
    }
  },

  methods: {
    toggleSidebar () {
      this.$emit('toggle-sidebar')
    },

    openOldPage () {
      window.open('/backup/mk1-2018.12.07/index.html')
    }
  }
}
</script>

<style scoped>
#app-main-header {
  position: fixed;
  top: 0;
  right: 0;
  transition: all .25s;
}
</style>
