<template>
  <mu-drawer
    id="app-sidebar"
    :open.sync="toggleDrawerOpen"
    :docked="isDrawerDocked"
    :width="sidebarWidth"
    :z-depth="drawerZDepth"
    v-resize="toggleDrawerDocked"
  >
    <!-- 导航头部 -->
    <mu-flex
      id="app-sidebar-header"
      justify-content="center"
      align-items="center"
    >
      <a
        class="avatar-container"
        href="https://github.com/tangshuaibo/tangshuaibo.github.io"
        target="_blank"
      >
        <mu-tooltip
          placement="bottom"
          content="查看代码"
        >
          <mu-avatar
            :size="68"
            class="avatar-image"
          >
            <img src="/img/avatar.jpeg">
          </mu-avatar>
        </mu-tooltip>
        <span class="avatar-name">
          唐帅博
        </span>
      </a>
    </mu-flex>
    <!-- 导航主体 -->
    <div id="app-sidebar-content">
      <app-sidebar-list
        @toggle-sidebar="closeSidebar"
      ></app-sidebar-list>
    </div>
    <!-- 导航底部 -->
    <div id="app-sidebar-footer">
      <mu-divider></mu-divider>
      <mu-list dense>
        <mu-list-item button>
          <mu-list-item-title>
            clear.lz@foxmail.com
          </mu-list-item-title>
          <mu-list-item-action>
            <mu-icon value="contact_mail"></mu-icon>
          </mu-list-item-action>
        </mu-list-item>
      </mu-list>
      <mu-tooltip
        placement="top"
        content="查看muse-ui"
      >
        <a
          class="footer-link"
          href="https://muse-ui.org/#/zh-CN"
          target="_blank"
        >
          Theme - Muse UI
        </a>
      </mu-tooltip>
    </div>
  </mu-drawer>
</template>

<script>
import AppSidebarList from '@/components/AppSidebarList.vue'

export default {
  name: 'app-sidebar',

  components: {
    'app-sidebar-list': AppSidebarList
  },

  props: {
    sidebarWidth: Number,
    sidebarToggleWidth: Number,
    isDrawerOpen: Boolean
  },

  data () {
    return {
      isDrawerDocked: false,
      drawerZDepth: 16
    }
  },

  computed: {
    toggleDrawerOpen: {
      get () {
        return this.isDrawerOpen
      },
      set () {
        this.$emit('toggle-sidebar')
      }
    }
  },

  methods: {
    toggleDrawerDocked () {
      if (window.innerWidth >= this.sidebarToggleWidth) {
        this.isDrawerDocked = true
        this.drawerZDepth = 1
      } else {
        this.isDrawerDocked = false
        this.drawerZDepth = 16
        if (this.isDrawerOpen) {
          this.$emit('toggle-sidebar')
        }
      }
    },

    closeSidebar () {
      this.$emit('close-sidebar')
    }
  }
}
</script>

<style scoped>
#app-sidebar {
  display: flex;
  flex-direction: column;
}

#app-sidebar-header {
  min-height: 144px;
  background-image: url(/img/drawer_header_background.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.avatar-container {
  display: block;
}

.avatar-image {
  border: 2px solid white;
}

.avatar-name {
  display: block;
  margin-top: 8px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  text-align: center;
  color: white;
}

#app-sidebar-content {
  flex: 1 1 auto;
  background-image: url(/img/drawer_content_background.jpg);
  background-size: 80% auto;
  background-repeat: no-repeat;
  background-position: right bottom;
  overflow: auto;
}

.footer-link {
  display: block;
  width: 100%;
  font-size: 14px;
  text-align: center;
  color: white;
  background-color: #212121;
}
</style>
