<template>
  <div
    id="app-main"
    :style="styleAppMain"
    v-resize="toggleOffsetLeft"
  >
    <!-- 应用头部 -->
    <app-header
      :sidebar-width="sidebarWidth"
      :is-drawer-open="isDrawerOpen"
      :is-offset-left="isOffsetLeft"
      @toggle-sidebar="toggleSidebar"
    ></app-header>
    <!-- 应用内容 -->
    <app-content></app-content>
  </div>
</template>

<script>
import AppHeader from '@/components/AppMainHeader.vue'
import AppContent from '@/components/AppMainContent.vue'

export default {
  name: 'app-main',

  components: {
    'app-header': AppHeader,
    'app-content': AppContent
  },

  props: {
    sidebarWidth: Number,
    sidebarToggleWidth: Number,
    isDrawerOpen: Boolean
  },

  data () {
    return {
      isOffsetLeft: false
    }
  },

  computed: {
    styleAppMain () {
      return {
        marginLeft: `${this.isDrawerOpen && this.isOffsetLeft ? this.sidebarWidth : 0}px`
      }
    }
  },

  methods: {
    toggleSidebar () {
      this.$emit('toggle-sidebar')
    },

    toggleOffsetLeft () {
      if (window.innerWidth >= this.sidebarToggleWidth) {
        this.isOffsetLeft = true
      } else {
        this.isOffsetLeft = false
      }
    }
  }
}
</script>

<style scoped>
#app-main {
  position: relative;
  transition: all .25s;
}
</style>
