import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      component: () => import('@/views/Error404.vue')
    },
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      component: () => import('@/views/AppHome.vue')
    },
    {
      path: '/music',
      component: () => import('@/views/AppMusic.vue')
    }
  ]
})
